<template>
    <div class="bottom-nav">
      <ul>
          <li :class="{active:currentItem===0}" @click="doSomething(0)">
              <router-link :to="{name:'home'}">
                  <i :class="'icon-home'"></i>
                  <div>1</div>
              </router-link>
          </li>
          <li :class="{active:currentItem===1}" @click="doSomething(1)">
              <router-link :to="{name:'category'}">
                  <i :class="'icon-category'"></i>
                  <div>2</div>
              </router-link>
          </li>
          <li :class="{active:currentItem===2}" @click="doSomething(2)">
              <router-link :to="{name:'cart'}">
                  <i :class="'icon-cart'"></i>
                  <div>3</div>
              </router-link>
          </li>
          <li :class="{active:currentItem===3}" @click="doSomething(3)">
              <router-link :to="{name:'member'}">
                  <i :class="'icon-user'"></i>
                  <div>4</div>
              </router-link>
          </li>

      </ul>
    </div>
</template>

<script>
    import qs from 'qs'

    let {index} = qs.parse(location.search.substr(1))//把路由上的参数?index=1 用qs解析成对象解构赋值给index

  let hrefmsg=[{
      icon: 'icon-home', name: 'home'
  },{
      icon: 'icon-category', name: 'category'
  },{
      icon: 'icon-cart', name: 'cart'
  },{
      icon: 'icon-user', name: 'member'
}]
    export default {
        data(){
          return {
            hrefmsg,
              currentItem:0
          }
        },
        methods:{
            doSomething(a){
                this.currentItem=a
            }
        }
    }
</script>

<style scoped>
.bottom-nav {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
  background-color: #fff;
  z-index: 8;
  -o-border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-top: 2px solid #e5e5e5
}

@media only screen and (-webkit-min-device-pixel-ratio:1.5),
only screen and (min--moz-device-pixel-ratio:1.5),
only screen and (min-device-pixel-ratio:1.5) {
  .bottom-nav {
    border-top-width: 1px
  }
}

.bottom-nav ul {
  position: relative;
  height: 100%;
  zoom: 1
}

.bottom-nav ul:after {
  content: "";
  display: table;
  clear: both
}

.bottom-nav li {
  display: block;
  float: left;
  width: 25%;
  height: 100%
}

.bottom-nav a {
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 8px;
  text-align: center;
  font-size: 10px
}

.bottom-nav [class^=icon-] {
  display: block;
  width: 22px;
  height: 22px;
  margin: auto;
  margin-bottom: 4px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% auto
}

.bottom-nav .icon-home {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-category {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-cart {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .icon-user {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active a {
  color: #ff6366
}

.bottom-nav .active .icon-home {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-category {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-cart {
  background-image: url("data:image/png;base64,")
}

.bottom-nav .active .icon-user {
  background-image: url("data:image/png;base64,")
}

</style>